function Node(item, nextNode) {
  this.item = item
  this.next = nextNode
}

export function Queue() {
  this.first = null
  this.last = null
  this.length = 0
}

Queue.prototype = {
  constructor: Queue,
  enqueue(x) {
    if(this.first === null) {
      this.first = this.last = new Node(x, null)
    } else {
      this.last.next = new Node(x, null)
      this.last = this.last.next
    }
    this.length++
  },
  dequeue() {
    let tmp = this.first
    if(tmp)
      this.first = this.first.next
    if(tmp === this.last && this.length === 1)
      this.last = null
    this.length--
    return tmp.item
  },
  isEmpty() {
    return this.first === null
  }
}
